import {
  useCertificateHistoryLogsQuery,
  useCertificateIssueNotExistStudentMutation,
  useCertificateNotExistInstituteMutation,
  useCertificateLeavingFormSettingMutation,
  useCertificateBonafideDublicateMutation,
  useCertificateBonafideStudentDetailQuery,
  useCertificateBonafideStudentUpdateDetailMutation,
  useCertificateRequestListQuery,
  useCertificateRequestExportMutation,
  useCertificateRequestIssueActionMutation,
  useCertificateLogsExportMutation,
} from "../../../Services/CertificateManager/certificateManagerApiDynamic";

export const useCertificateHistoryLogs = (args) => {
  const { data, refetch, isFetching } = useCertificateHistoryLogsQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    certificateHistoryLogs: data,
    certificateHistoryLogsRefetch: refetch,
    certificateHistoryLogsLoading: isFetching,
  };
};

export const useCertificateIssueNotExistStudent = () => {
  const [certificateIssueNotExistStudent] =
    useCertificateIssueNotExistStudentMutation();
  return [certificateIssueNotExistStudent];
};
