import { PATCH } from "../UrlConfig/MethodType";

export const certificateIssueNotExistStudentFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/extra/leaving/certificate/not/exist/ins/${data?.id}`,
      method: PATCH,
      body: data.bodyParameter,
    }),
  });
};

